import React, { PureComponent } from 'react';
import '../css/Person.css';
import '../css/App.css';
const random = require('random');
let interval = null;

export class Person extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            pers : null
        }
    }

    componentDidMount() {
        try{
            let user = this.props.user;
            let persons = this.props.persons;
            let i = this.props.person;
            let skin = this.props.hasOwnProperty('skin') ?
                persons[i].skins[this.props.skin].url
                :
                user.hasOwnProperty('active_skins') ?
                    user.active_skins.hasOwnProperty(i+'') ?
                        user.active_skins[i] === null ?
                            persons[i].skins[0].url
                            :
                            persons[i].skins[user.active_skins[i]].url
                        :
                        persons[i].skins[0].url
                    :
                    persons[i].skins[0].url;

            if(this.props.is_game){
                let used = false;
                let transform = random.int(-40, 40);
                interval = setInterval(()=>{
                    let chance = random.float(0,1);
                    if(chance<=0.25){
                        if(i === 1){
                            transform = -transform;
                            this.setState({ pers:
                                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <img src={skin} onClick={this.props.onClick} style={{ height: '170px', transform: 'translate('+transform+'%)' }}/>
                                    </div>
                            });
                        }else if (i === 2){
                            let pi = random.int(1,2);
                            if(!used){
                                used = true;
                                if(pi===1){
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} style={{ height: '110px' }}/>
                                                <img src={skin} onClick={this.props.onClick} style={{ marginLeft: "25px", height: '110px' }}/>
                                            </div>
                                    });
                                }else{
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} onClick={this.props.onClick} style={{ marginLeft: "25px", height: '110px' }}/>
                                                <img src={skin} style={{ height: '110px' }}/>
                                            </div>
                                    });
                                }
                            }else{
                                used = false;
                                this.setState({ pers:
                                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                            <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                        </div>
                                });
                            }
                        }else if (i === 3){
                            if(!used){
                                used = true;
                                this.setState({ pers:
                                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                            <img className={'hr jump'} src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                        </div>
                                });
                                setTimeout(()=>{
                                    used = false;
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                            </div>
                                    });
                                }, 1250);
                            }
                        }
                    }
                },400);
            }
            this.setState({ pers:
                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                        <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                    </div>
            });
        }catch (e) {
            
        }
    }

    render() {
        return (
            this.state.pers
        )
    }

}