import React, { PureComponent } from 'react';
import {Tappable} from '@vkontakte/vkui';

export class ColorButton extends PureComponent {

    render() {
        let color = this.props.color;
        let background_color = 'rgba'+color.substring(color.indexOf('('), color.indexOf(')'))+',0.12)';
        return (
            <Tappable style={{ borderRadius: '14px', marginBottom: '10px' }} onClick={this.props.onClick}>
                <div style={{ backgroundColor: background_color, color: color, padding: '17px 18px', borderRadius: '14px', fontWeight: '500', fontSize: '20px', lineHeight: '22px', backgroundPosition: '100% 100%', backgroundRepeat: 'no-repeat', position: 'relative', overflow: 'hidden' }}>
                    <span style={{ position: 'relative', zIndex: '2' }}>{this.props.children}</span>
                    <div style={{ margin: 10, backgroundImage: 'url("'+this.props.icon+'")', opacity: 0.5, zIndex: '1', position: 'absolute', content: "", width: '60px', height: '60px', backgroundSize: '60px', backgroundPosition: '50%', backgroundRepeat: 'no-repeat', right: '5px', top: '5%' }}/>
                </div>
            </Tappable>
        )
    }

}