import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import '../css/Popout.css';

export class Popout extends PureComponent {


    constructor(props) {
        super(props);

        this.state = {
            anim_started: false,
            isShow: false
        };

        this.show_popout = this.show_popout.bind(this);
        this.close_popout = this.close_popout.bind(this);
    }

    show_popout() {
        if (!this.state.anim_started) {
            if(document.getElementById('other').className === 'other other_show') return;
            this.setState({ anim_started: true });
            setTimeout(() => this.setState({ anim_started: false }), 250);
            this.setState({ isShow: true });
            document.getElementById('other').className = 'other other_show';
            document.getElementById('top').className = 'top top_show';
        }
    }

    close_popout() {
        if (!this.state.anim_started) {
            this.setState({ anim_started: true });
            setTimeout(() =>{
                this.setState({ anim_started: false, isShow: false });
                this.props.onClose();
            }, 250);
            document.getElementById('other').className = 'other other_close';
            document.getElementById('top').className = 'top top_close';
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.isShow){
            this.show_popout();
        }
    }

    render() {
        let { isShow, children } = this.props;
        this.setState({ isShow });

        return (
            <div id='popout' style={{ display: isShow ? 'block' : 'none' }}>
                <div id='top' className='top' style={{ zIndex: 1001 }}>
                    {children}
                </div>
                <div id='other' className='other' onClick={()=>{ this.close_popout(); }} style={{ zIndex: 1000 }}></div>
            </div>
        )
    }
}

Popout.propTypes = {
    onClose: PropTypes.func,
    isShow: PropTypes.bool
};

export default Popout;