import React, { PureComponent } from 'react';
import {Tappable, Separator} from '@vkontakte/vkui';

let scheme;
export class Card extends PureComponent {

    render() {
        let scheme;
        try{
            scheme  = document.body.attributes.getNamedItem('scheme').value;
        }catch (e) {
            scheme = 'client_light';
        }
        let { Card } = require('@vkontakte/vkui');
        const btn = <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', color: scheme === 'space_gray' ? '' : '#3f8ae0' }}>
            { this.props.button_icon }
            <p style={{ marginLeft: 8 }}>{this.props.button_text}</p>
        </div>;
        return (
            <Card size="s" style={{ borderRadius: '14px', background: scheme === 'space_gray' ? '' : 'white', margin: '16px 6px', boxShadow: '0 0 2px 0 rgba(0,0,0,.08), 0 2px 20px 0 rgba(0,0,0,.06)' }}>
                <div style={{ width: '250px', minHeight: '274px', height: '100%' }}>
                    <div style={{ padding: '20px' }}>
                        {
                            this.props.hasOwnProperty('icon') &&
                            <div style={{ background: 'url("'+this.props.icon+'")', backgroundRepeat: 'no-repeat', backgroundSize: '44px 44px', height: '44px', marginBottom: '8px' }}/>
                        }
                        <div style={{ marginBottom: '9px', fontSize: '20px', lineHeight: '24px', fontWeight: '600' }}>{this.props.title}</div>
                        {
                            this.props.hasOwnProperty('text') &&
                            <div style={{ fontSize: '16px', color: scheme === 'space_gray' ? '' : '#818c99', lineHeight: '20px', paddingBottom: '50px' }}>{this.props.text}</div>
                        }
                        {
                            this.props.hasOwnProperty('button_text') &&
                            <div style={{ margin: '17px -20px -20px' }}>
                                <div style={{ position: 'absolute', bottom: '0', width: '100%' }}>
                                    <Separator wide/>
                                    {
                                        this.props.disabled ?
                                            <div style={{ opacity: 0.5 }}>
                                                {btn}
                                            </div>
                                            :
                                            <Tappable style={{ borderRadius: '0px 0px 14px 14px' }} onClick={this.props.onClick}>
                                                {btn}
                                            </Tappable>
                                    }
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </Card>
        )
    }

}