import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/MainButtons.css';
import {Gallery} from '@vkontakte/vkui';

export class MainButtons extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            slideIndex: 0
        }
    }

    render() {
        let scheme;
        try{
            scheme  = document.body.attributes.getNamedItem('scheme').value;
        }catch (e) {
            scheme = 'client_light';
        }
        let buttons_array = [];
        let buttons = [];
        let i_ = 0;
        React.Children.map(this.props.children, (child, i) => {
            if(child !== null) buttons.push(<div className={'btn'}>{child}</div>); else i_--;

            if( (i_+1) % 3 === 0){
                if(buttons.length>0){
                    buttons_array.push(<div style={{display: 'flex', borderRadius: "20px"}}>{buttons}</div>);
                    buttons = [];
                }
            }
            if(i === (this.props.children.length-1) && buttons.length > 0){
                if(buttons.length>0){
                    buttons_array.push(<div style={{display: 'flex', borderRadius: "20px"}}>{buttons}</div>);
                    buttons = [];
                }
            }
            i_++;
        });
        return (
            <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}>
                <div className={'background'} style={{ background: scheme === 'space_gray' ? 'linear-gradient(135deg, rgba(69,70,71,1) 0%, rgba(98,100,102,1) 100%)' : 'linear-gradient(135deg, rgba(97,144,232,1) 0%, rgba(167,191,232,1) 100%)' }}>
                    <Gallery
                        bullets={buttons_array.length > 1 ? 'light' : false}
                        slideWidth="100%"
                        align="center"
                        slideIndex={this.state.slideIndex}
                        style={{height: "100%", width: "100%"}}
                        onChange={i => {
                            this.setState({slideIndex: i})
                        }}
                    >
                        {buttons_array}
                    </Gallery>
                </div>
            </div>
        )
    }

}