import React, { PureComponent } from 'react';
import '../css/LeftHeader.css';

export class LeftHeader extends PureComponent {

    render() {
        let scheme;
        try{
            scheme  = document.body.attributes.getNamedItem('scheme').value;
        }catch (e) {
            scheme = 'client_light';
        }
        return (
            scheme === 'space_gray' ?
                <div onClick={this.props.onClick} style={this.props.style} className={'container-dark'}>
                    {this.props.title}
                </div>
                :
                <div onClick={this.props.onClick} style={this.props.style} className={'container'}>
                    {this.props.title}
                </div>

        )
    }

}