import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/App.css';
import { lighten, withStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';

const BorderLinearProgress = withStyles({
    root: {
        height: 10,
        borderRadius: 20,
        backgroundColor: lighten('rgba(92,156,230,1)', 0.5),
    },
    bar: {
        borderRadius: 20,
        backgroundColor: 'rgba(92,156,230,1)',
    },
})(LinearProgress);

export class MaterialProgress extends PureComponent {

    render() {
        return (
            <BorderLinearProgress variant="determinate" {...this.props}/>
        )
    }

}